.bottomBar {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: var(--gray-50);
  padding-bottom: 2rem;
}

.socials {
  position: relative;
  width: 25%;
  margin: auto;
  left: 0;
  right: 0;
  margin-top: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.socialButton {
  width: 2rem;
  padding: 0.5rem;
  background-color: var(--violet-200);
  background-size: 100% 200%;
  background-position: 100% 25%;
  transition: 0.5s;
  cursor: pointer;
}

.socialButton:hover {
  background-color: var(--violet-300);
}

.links {
  position: relative;
  width: 35%;
  font-size: var(--font-12);
  margin: auto;
  left: 0;
  right: 0;
  margin-top: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link {
  color: var(--light);
  font-weight: var(--bold);
  cursor: pointer;
}

.link:hover {
  color: var(--gray-200);
}

.logo {
  position: relative;
  width: 5rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  cursor: pointer;
  transition: 0.2s;
}

.logo:hover {
  filter: brightness(0.8);
}

.signature {
  color: var(--gray-300);
  font-size: var(--font-14);
  font-weight: var(--thin);
}

@media screen and (max-width: 480px) {
  .socials {
    width: 90%;
  }

  .links {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    justify-content: space-between;
  }

  .link {
    width: 30%;
    height: 3rem;
    text-align: center;
  }
}
